import React from "react";
import { Box, IconButton, InputBase } from "@material-ui/core";
import RemoveIcon from "@material-ui/icons/Remove";
import AddIcon from "@material-ui/icons/Add";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
  remove: {
    backgroundColor: "#bdbdbd",
    borderRadius: 0,
    borderTopLeftRadius: "5px",
    borderBottomLeftRadius: "5px",
    color: "#ffffff",
  },
  add: {
    backgroundColor: "#bdbdbd",
    borderRadius: 0,
    borderTopRightRadius: "5px",
    borderBottomRightRadius: "5px",
    color: "#ffffff",
  },
});

function InputJumlahProduk() {
  const classes = useStyles();
  return (
    <React.Fragment>
      <Box display="flex">
        <IconButton className={classes.remove} size="small">
          <RemoveIcon />
        </IconButton>
        <Box border="1px solid #ddd" display="flex">
          <InputBase type="number" defaultValue={1} />
        </Box>
        <IconButton className={classes.add} size="small">
          <AddIcon />
        </IconButton>
      </Box>
    </React.Fragment>
  );
}

export default InputJumlahProduk;
