import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import BottomNavigation from "@material-ui/core/BottomNavigation";
import BottomNavigationAction from "@material-ui/core/BottomNavigationAction";
import AddShoppingCartIcon from "@material-ui/icons/AddShoppingCart";
import { DialogProduk } from "components";

const useStyles = makeStyles({
  root: {
    width: "100%",
    height: "50px",
    position: "fixed",
    display: "flex",
    bottom: 0,
  },
  beli: {
    background: "linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)",
    color: "#ffffff",
    maxWidth: "100%",
    width: "50%",
  },
  keranjang: {
    maxWidth: "100%",
    width: "50%",
  },
  label: {
    fontSize: "1rem",
    fontWeight: "bold",
  },
});

function BottomNav(props) {
  const classes = useStyles();

  const [open, setOpen] = React.useState({ open: false, title: "" });

  const handleOpenKeranjang = () => {
    setOpen({ ...open, open: true, title: "Tambah Ke Keranjang Belanja" });
  };
  const handleOpenBeli = () => {
    setOpen({ ...open, open: true, title: "Beli Sekarang" });
  };
  const handleClose = () => {
    setOpen({ ...open, open: false, title: "" });
  };

  return (
    <React.Fragment>
      <BottomNavigation showLabels className={classes.root}>
        <BottomNavigationAction
          onClick={handleOpenKeranjang}
          className={classes.keranjang}
          icon={<AddShoppingCartIcon />}
        />
        <BottomNavigationAction
          onClick={handleOpenBeli}
          className={classes.beli}
          classes={{ label: classes.label }}
          label="Beli Sekarang"
        />
      </BottomNavigation>
      <DialogProduk
        open={open.open}
        handleClose={handleClose}
        title={open.title}
      />
    </React.Fragment>
  );
}

export default BottomNav;
